.hero-banner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    span {
        display: block;
    }

    button {
        display: block;
        background-color: #D0992E;
        color: #fff;
        padding: 15px 10px;
        width: 100px;
        margin-top: 15px;
        border-radius: 10px;
        border: none;
        cursor: pointer;
        transition: color 0.4s;
        transition: transform 0.4s;

        &:hover {
            transform: scale(1.2);
            color: #000;
        }
    }

    h1 { 
        color:  #fff;
        z-index: 2;
        padding: 0 1rem;
        color: #fff;
        line-height: 1.5;
        font-weight: normal;
        letter-spacing: 4px;
        text-transform: uppercase;
        font-weight: 600;
        margin-top: -160px;
        text-align: center;
    }

    .overlay {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #000000;
        opacity: .7;
    }

    .subheading {
        position: inherit;
        text-align: center;
        font-size: 1em;
    }

    @media (min-width: 990px) {
        h1 {
            font-size: 60px;
        }

        button {
            font-size: 24px;
            color: #fff;
            line-height: 1.5;
            font-weight: normal;
            letter-spacing: 4px;
            text-transform: uppercase;
            font-weight: 600;
            width: 180px;
        }
    }


}

.home .hero-banner {
    height: 100vh;
}