body {
margin: 0;
}

p {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  color: #666666;
  font-family: 'Roboto', sans-serif;
}

a {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 2;
  font-weight: 400;
  color: #666;
  text-decoration: none;
  transition: color 0.4s ease-in-out;

  &:hover {
    color: #000;
  }
}

h1 {
  position: relative;
  font-family: 'Roboto', sans-serif;
  width: 100%;
  text-align: center;
}

h2 {
  width: 100%;
  margin-top: 60px;
  text-align: center;
  font-size: 40px;
  position: relative;
  font-family: 'Roboto', sans-serif;
}

h3 {
  font-family: 'Roboto', sans-serif;

}

.subheading {
    position: absolute;
    top: -20px;
    left: 0;
    right: 0;
    font-weight: normal;
    font-size: 1.5em;
    display: block;
    margin-bottom: 0;
    color: #D0992E;
    line-height: 1;
    font-family: "Great Vibes", cursive;
    margin-top: 0;
    z-index: -1;
}



@media (min-width: 768px) {
  .container {
      max-width: 1170px;
      margin: 0 auto;
  }
}
