.navbar {
    position: absolute;
    width: 100%;
    background-color: rgba(0, 0, 0, 1);
    z-index: 3;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    
    p,a {
        font-size: 17px;
        color: #fff;
        line-height: 1;
        text-decoration: none;

        &:hover {
            color: #D0992E;
        }
    }

    a.is-active {
        color: #D0992E;
    }

    .container {
        padding: 1rem;
    }
    
    &-mobile {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    &-toggler {
        display: flex;
        background-color: #000;
        border: none;
        cursor: pointer;
        padding-right: 0;
        text-transform: uppercase;
        font-size: 16px;
        letter-spacing: .1em;
        
        &:focus {
            outline: auto;
        }

        span {
            margin: auto 0 auto 10px;
            color: #6c757d;
            line-height: inherit;
        }
    }

    &-burger {
        .bar1, .bar2, .bar3 {
            display: block;
            width: 25px;
            height: 3px;
            background-color: #6c757d;
            margin: 4px 0;
            transition: 0.4s;
        }


        &.is-active{
             .bar1 {
            transform: rotate(-45deg) translate(-2px, 6px) ;
        }
 
            .bar2 {
                opacity: 0;
            }

            .bar3 {
                transform: rotate(45deg) translate(-4px, -8px) ;
            }
        }
    }

    .navbar-collapse {
        z-index: 4;
        max-height: 0px;
        overflow: hidden;
        transition: max-height 0.4s ease;

        ul {
            color: #fff;
            padding: 0;
            margin: 0;
        }

        li {
            list-style-type: none;
            padding: 1rem 0;
            cursor: pointer;

            p,
            a {
                transition: color 0.4s ease-in-out;
            }

            &:last-of-type {
                padding-bottom: 0;

            }

            &:hover a{
                color:  #D0992E;
            }
        }

        .make-order-cta {
            max-width: 320px;
            padding: 15px 10px;
            background-color: #D0992E;
            border-radius: 5px;
        }

        &.is-active {
            max-height: 300px;
            display: block;
        }
    }

    .navbar-brand {
        font-weight: 900;
        font-size: 20px;
        color: #fff;
        cursor: pointer;
    }

    p {
        margin: 0;
    }

    @media (min-width: 990px) {
        background-color: rgba(0, 0, 0, 0.4);

        .navbar-brand {
            margin: auto 0;
        }

        .container {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        &-toggler {
            display: none;
        }

        .navbar-collapse {
            display: block;
            max-height: inherit;

            ul {
                li {
                    display: inline-block;
                    padding: 0 15px;
                }
            }
        }
    }

    &.fixed {
        position: fixed;
        background-color: #fff;
        z-index: 6;

        .navbar-brand {
            color: #000;
        }

        button {
            background-color: #fff;
        }

        .navbar-collapse ul p {
            color: #000;
        }
    }
}