.tiles {
    position: relative;
    
    >div {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        padding: 2rem 0;
    }

    .tile-image {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
    
    .tiles-title {
        padding: 0 1rem;
    }

    @media (min-width: 990px) {
        .tiles-title {
            max-width: 1170px;
            margin: 0 auto;
        }
    }


    .theme-1 {
        display: none;     

        @media (min-width: 990px) {
            display: flex;
            width: 100%;
            padding: 0;
            position: absolute;
            top: -230px;
            border-top: 1px solid rgba(255, 255, 255, 0.1);

            .tile {
                max-width: 300px;
                padding: 1rem;
                flex: 1 1 20%;

                &-image {
                    width: 90px;
                    height: 90px;
                    border-radius: 80px;
                    margin: 1rem auto;
                }
            }

            h3 {
                margin: 0.4rem 0;
                line-height: 1;
            }

            p {
                margin: 0.4rem 0;
                line-height: 1;
                font-weight: 400;
            }   

            p,
            h3 {
                color: rgba(255, 255, 255, 0.8);
                text-align: center;
            }
        }
    }

    .theme-2 {
        padding: 0 1rem 1rem;

        .tile {
            text-align: left;
            border: 1px solid #e6e6e6;

            &-text {
                padding: 1rem;
            }

            &-image {
                width: 100%;
                height: 250px;
                margin: 0 auto;
            }

            &-heading {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                
                p {
                    flex: 1 1 30%;
                    text-align: right;
                    color: #D0992E;
                    font-weight: bold;
                }

                h3 {
                    flex: 1 1 70%;
                }
            }
        }

        
        @media (min-width: 990px) {
            max-width: 1170px;
            margin: 0 auto;
            padding: 0;
            border: 1px solid #e6e6e6;

            .tile {
                display: flex;
                flex-direction: row;
                width: 50%;
                box-sizing: border-box;
                border: 0;

                &:nth-of-type(n+3) {
                    flex-direction: row-reverse;
                }

                &-image {
                    flex: 1 1 50%;
                }

                &-text {
                    flex: 1 1 50%;
                    padding: 0;

                    >div {
                        padding: 0 1rem;
                        margin: auto 0;
                    }
                }
            }
        }
    }
}